<template>
  <cpcBaseRefresh ref="refreshScroll" @loading="loading" @loadMore="loadMore">
    <div slot="statePulldown">
      <i class="iconfont icon-down-arrow"></i>
      下拉刷新
    </div>
    <div slot="stateRelease">
      <i class="iconfont icon-refreshup"></i>
      释放刷新
    </div>
    <div slot="stateRefreshing">
      <loading2></loading2>
      刷新中
      <morePoint></morePoint>
    </div>
    <div slot="stateRefreshEnd">
      <finishedTick :isShow="true"></finishedTick>
      刷新完成
    </div>
    <slot></slot>
    <div slot="loadMoreing">
      <loading2></loading2>
      加载中
      <morePoint></morePoint>
    </div>
    <div slot="loadMores">加载更多</div>
    <div slot="loadMoreALL">加载全部了</div>
  </cpcBaseRefresh>
</template>

<script>
  import cpcBaseRefresh from '../cpcBaseRefresh.vue'
  import loading2 from '../../animate/loading/cpcLoading2.vue';
  import finishedTick from '../../animate/finishedTick/cpcFinishedTick.vue';
  import morePoint from '../../animate/morePoint/cpcMorePoint.vue';

  export default {
    components: {cpcBaseRefresh, loading2,finishedTick,morePoint},
    data() {
      return {}
    },
    methods: {
      loading(finishPullDown) {
        this.$emit("loading",finishPullDown)
      },
      loadMore(finishpullUp,setLoadMoreALL) {
        this.$emit("loadMore",finishpullUp,setLoadMoreALL)
      }
    }
  }
</script>

<style scoped>

</style>



