<template>
  <div class="single-row1">
    <slot name="left"></slot>
    <div class="center">
      <slot name="center"></slot>
    </div>
    <slot name="right" class="right"></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    components: {},
    data() {
      return {}
    },
    props: {},
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .single-row1 {
    background-color: #fff;
    display: flex;
    .center {
      flex: 1;
      overflow: hidden;
    }
  }
</style>



