<template>
  <cpcBaseRefresh ref="refreshScroll" @loading="loading" @loadMore="loadMore">
    <span slot="statePulldown">下拉刷新</span>
    <span slot="stateRelease">释放刷新</span>
    <span slot="stateRefreshing">刷新中</span>
    <span slot="stateRefreshEnd">刷新完成</span>
    <slot></slot>
    <loading slot="loadMoreing"></loading>
    <span slot="loadMores">加载更多</span>
    <span slot="loadMoreALL">加载全部了</span>
  </cpcBaseRefresh>
</template>

<script>
  import cpcBaseRefresh from '../cpcBaseRefresh.vue'
  import loading from '../../animate/loading/cpcLoading.vue';

  export default {
    components: {cpcBaseRefresh, loading},
    data() {
      return {}
    },
    methods: {
      loading(finishPullDown) {
        this.$emit("loading",finishPullDown)
      },
      loadMore(finishpullUp,setLoadMoreALL) {
        this.$emit("loadMore",finishpullUp,setLoadMoreALL)
      }
    }
  }
</script>

<style scoped>

</style>



