<template>
  <cpcBaseTopNav>
    <i slot="left" class="left iconfont icon-geren1" @click="$emit('clickLeft')"></i>
    <p slot="center">
      <span>{{title}}</span>
    </p>
    <i slot="right" class=" right iconfont icon-shouye1" @click="$emit('clickRight')"></i>
  </cpcBaseTopNav>
</template>

<script type="text/ecmascript-6">
  import cpcBaseTopNav  from '../cpcBaseTopNav.vue';
    export default {
        components: {cpcBaseTopNav},
        data() {
            return {}
        },
        props: {
          title:{
            type:String,
            default:""
          },
        },
        methods: {},
        computed: {},
        mounted() {

        },
        destroyed() {

        }
    }
</script>

<style scoped lang="less">
  .left{
    color: #666;
    font-size: 0.25rem;
  }
  .right{
    color: #666;
    font-size: 0.25rem;
  }
</style>



