<template>
  <cpcHeadAndScroll title="主页" @clickLeft="clickLeft" @clickRight="clickRight">
    <cpcGrid :datas="lists"></cpcGrid>
  </cpcHeadAndScroll>
</template>

<script>
  import cpcHeadAndScroll from '../CPCUI/scroll/headAndScroll/cpcHeadAndScroll.vue'
  import cpcGrid from '../CPCUI/grid/cpcGrid.vue'

  export default {
    components: {cpcHeadAndScroll, cpcGrid},
    name: 'HelloWorld',
    data() {
      return {
        lists: [
          {
            url: "/cpcBaseScroll",
            icon: "icon-shangxiahuadong",
            label: "滑动基类"
          }, {
            url: "/cpcBackHeadAndScroll",
            icon: "icon-shangxiahuadong",
            label: "滑动带头部"
          }, {
            url: "/customRefreshScroll",
            icon: "icon-loading5",
            label: "自定义刷新"
          }, {
            url: "/cpcRefreshType1",
            icon: "icon-loading5",
            label: "刷新1"
          }, {
            url: "/cpcRefreshType2",
            icon: "icon-loading5",
            label: "刷新2"
          },{
            url: "/cpcBackHeadAndRefresh",
            icon: "icon-loading5",
            label: "刷新带头部"
          }, {
            url: "/animate",
            icon: "icon-donghua",
            label: "动画"
          }, {
            url: "/card",
            icon: "icon-qiapian",
            label: "卡片"
          }, {
            url: "/topNav",
            icon: "icon-yemiantoubu",
            label: "头部栏"
          }, {
            url: "/banner",
            icon: "icon-yemiantoubu",
            label: "轮播图"
          }, {
            url: "/toast",
            icon: "icon-yemiantoubu",
            label: "吐司"
          }

        ]
      }
    },
    methods: {
      clickLeft(){
        console.log("左边点击了")
      },
      clickRight(){
        console.log("右边点击了")
      }
    }
  }
</script>

<style scoped lang="less">
  @borderLine: 0.01rem solid #e0e0e0;
  .mainPage {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    border-top: @borderLine;
    border-left: @borderLine;
    li {
      background-color: #fff;
      height: 0.9rem;
      line-height: 0.9rem;
      text-align: center;
      box-sizing: border-box;
      font-size: 0.12rem;
      width: 25%;
      overflow: hidden;
      border-bottom: @borderLine;
      border-right: @borderLine;
    }
  }
</style>



