<template>
  <cpcBackHeadAndScroll title="动画">
    <p style="padding: 0.1rem"></p>
    <cpcChecked></cpcChecked>
    <p style="padding: 0.1rem"></p>
    <button @click="isFinish=!isFinish">isFinish</button>
    <cpcFinishedTick v-if="isFinish"></cpcFinishedTick>
    <p style="padding: 0.1rem"></p>
    <cpcLoading :size="0.5" bgColor="#f00"></cpcLoading>
    <p style="padding: 0.1rem"></p>
    <cpcLoading2></cpcLoading2>
    <p style="padding: 0.1rem"></p>
    <cpcMorePoint></cpcMorePoint>
    <p style="padding: 0.1rem"></p>
  </cpcBackHeadAndScroll>
</template>

<script type="text/ecmascript-6">
  import cpcBackHeadAndScroll from '../../../CPCUI/scroll/headAndScroll/cpcBackHeadAndScroll.vue'
  import cpcChecked from '../../../CPCUI/animate/checked/cpcChecked.vue'
  import cpcFinishedTick from '../../../CPCUI/animate/finishedTick/cpcFinishedTick.vue'
  import cpcLoading from '../../../CPCUI/animate/loading/cpcLoading.vue'
  import cpcLoading2 from '../../../CPCUI/animate/loading/cpcLoading2.vue'
  import cpcMorePoint from '../../../CPCUI/animate/morePoint/cpcMorePoint.vue'

  export default {
    components: {cpcBackHeadAndScroll,cpcChecked,cpcFinishedTick,cpcLoading,cpcLoading2,cpcMorePoint},
    data() {
      return {
        isFinish:true
      }
    },
    props: {},
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">

</style>



