<template>
  <div style="width: 0.3rem;height: 0.2rem;display: inline-block;">
    <div class="more-point">
      ...
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    components: {},
    data() {
      return {}
    },
    props: {},
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .more-point {
    line-height: 0.2rem;
    font-size: 0.2rem;
    width: 0rem;
    height: 0.2rem;
    overflow: hidden;
    -moz-animation: loadMore 1s ease 0ms forwards infinite;
    -o-animation: loadMore 1s ease 0ms forwards infinite;
    -webkit-animation: loadMore 1s ease 0ms forwards infinite;
    animation: loadMore 1s ease 0ms forwards infinite;
  }

  @keyframes loadMore {
    0% {
      width: 0;
    }
    100% {
      width: 0.3rem;
    }
  }

  @-webkit-keyframes loadMore {
    0% {
      width: 0;
    }
    100% {
      width: 0.3rem;
    }
  }

  @-moz-keyframes loadMore {
    0% {
      width: 0;
    }
    100% {
      width: 0.3rem;
    }
  }

</style>



