<template>
  <div class="cpcBanner2">
    <!-- swiper1 -->
    <swiper :options="swiperOptionTop" class="gallery-top" ref="swiperTop">
      <swiper-slide v-for="item in datas" :style="{backgroundImage:'url('+ item.img+')'}">
      </swiper-slide>
    </swiper>
    <!-- swiper2 Thumbs -->
    <swiper :options="swiperOptionThumbs" class="gallery-thumbs" ref="swiperThumbs">
      <swiper-slide v-for="item in datas" :style="{backgroundImage:'url('+ item.img+')'}"></swiper-slide>
    </swiper>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    components: {},
    data() {
      return {
        datas: [
          {
            img: "./static/images/img1.jpg",
            url: "/"
          },
          {
            img: "./static/images/img4.jpg",
            url: "/"
          },
          {
            img: "./static/images/img3.jpg",
            url: "/"
          },
          {
            img: "./static/images/img2.jpg",
            url: "/"
          },
          {
            img: "./static/images/img1.jpg",
            url: "/"
          },
          {
            img: "./static/images/img0.jpg",
            url: "/"
          }
        ],

        swiperOptionTop: {
          spaceBetween: 0,
//          loop: true,
//          loopedSlides: 5, //循环播放的幻灯片应该是一样的
        },
        swiperOptionThumbs: {
          spaceBetween: 10,
          slidesPerView: 4,
          touchRatio: 0.2,
          centeredSlides: true,
//          loop: true,
//          loopedSlides: 5, //循环播放的幻灯片应该是一样的
          slideToClickedSlide: true,
        }
      }
    },
    props: {},
    methods: {
      changeActive(e) {
        let url = e.target.dataset.bindurl;
        this.$router.push(url)
      }
    },
    computed: {},
    mounted() {
      this.$nextTick(() => {
        const swiperTop = this.$refs.swiperTop.swiper
        const swiperThumbs = this.$refs.swiperThumbs.swiper
        swiperTop.controller.control = swiperThumbs
        swiperThumbs.controller.control = swiperTop
      })
    },
    destroyed() {

    }
  }
</script>

<style lang="less" scoped>
  .cpcBanner2 {
    height: 4.5rem;
    .swiper-container {
      background-color: #548;
    }
    .swiper-slide {
      background-size: cover;
      background-position: center;
    }
    .gallery-top {
      height: 80% !important;
      width: 100%;
    }
    .gallery-thumbs {
      height: 20% !important;
      box-sizing: border-box;
      padding: 10px 0;
    }
    .gallery-thumbs .swiper-slide {
      width: 25%;
      height: 100%;
      opacity: 0.4;
    }
    .gallery-thumbs .swiper-slide-active {
      opacity: 1;
    }
  }
</style>



