<template>
  <div style="height: 100%;">
    <ul class="tabs">
      <li @click="ss(0)">
        item1
      </li>
      <li @click="ss(1)">
        item2
      </li>
    </ul>
    <swiper ref="mySwiper" :options="swiperOption" class="swiper-box">
      <swiper-slide class="swiper-item" >
        <iframe class="itemIframe" id="iframeId" src="http://www.baidu.com" frameborder=0  name="showHere"
                scrolling=auto></iframe>
      </swiper-slide>
      <swiper-slide class="swiper-item" style="pointer-events: none;">
        <!--<div style="width: 100%;height: 100%;" id="viewDiv"></div>-->
        <iframe class="itemIframe" src="http://www.qq.com" frameborder=0  name="showHere" scrolling=auto></iframe>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        swiperOption: {
          on: {
            touchStart: function (event) {
//              alert('事件触发了;');
            },
          },
          slidesPerView: 1,
          spaceBetween: 30,
          mousewheel: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          }
        }
      }
    },
    methods: {
      ss(i) {
        this.$refs.mySwiper.swiper.slideTo(i, 0)
      },
      kkk() {
        var myswiper = this.$refs.mySwiper.swiper;
      //index 是swiper的下标,sum是我嵌套了几个iframe页面
        var index;
        var sum;
        //因为我的需求是左右滑动，所以上下滑动的监听看需求
        var startX, startY, endX, endY, distanceX, distanceY;
        //iframeId  就是iframe的id
        var iframe  = document.getElementById("iframeId").contentWindow.document
//        var iframe = document.getElementById("iframeId").contentWindow.document;
        iframe.addEventListener('touchstart', function (e) {
          startX = e.targetTouches[0].pageX;
          startY = e.targetTouches[0].pageY;
        });
        iframe.addEventListener('touchemove', function (e) {
          console.log("我来试试")
          endX = e.targetTouches[0].pageX;
          endY = e.targetTouches[0].pageY;
          distanceX = endX - startX;
          distanceY = endY - startY;
          //后面的判断条件根据情况添加以及判断
          //增加与Y轴的比较是当往上滑动的距离大于往右的距离时，取其往上滑动的距离，即往上滑动
          //这里还需要注意一点，滑动的距离需要调整  我是当滑动距离大于当前屏幕一半的时候为触发条件
          //还有就是最好跳转至指定的页面，因为我有时候一下滑动了好几个页面
          if (Math.abs(distanceX) > Math.abs(distanceY) && distanceX > ($(window).width() / 2)) {
            if (index != 0) {
              myswiper.slideTo(index - 1);
            }
            console.log('往右滑动');
          } else if (Math.abs(distanceX) > Math.abs(distanceY) && distanceX < -($(window).width() / 2)) {
            if (index != (sum - 1)) {
              myswiper.slideTo(index + 1);
            }
            console.log('往左滑动');
          } else if (Math.abs(distanceX) < Math.abs(distanceY) && distanceY < 0) {
            console.log('往上滑动');
          } else if (Math.abs(distanceX) < Math.abs(distanceY) && distanceY > 0) {
            console.log('往下滑动');
          } else {
            console.log('点击未滑动');
          }
        });
      }
    },
    mounted() {
      this.kkk();
    },
    destroyed() {

    }
  }
</script>

<style lang="less">

  .tabs {
    display: flex;
    text-align: center;
    li {
      flex: 1;
    }
  }

  .swiper-box {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  .swiper-item {
    height: 100%;
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    .itemIframe {
      height: 90%;
      width: 100%;
    }
  }
</style>



