<template>
  <ul class="mainPage">
    <router-link v-for="item in datas" :to="item.url" tag="li" :style="{backgroundColor:getRandomColor()}">
      <i v-if="item.icon != ''" class="iconfont" :class="item.icon"></i>
      <p style="margin-top: 0.05rem">{{item.label}}</p>
    </router-link>
  </ul>
</template>

<script>

  export default {
    components: {},
    name: 'HelloWorld',
    data() {
      return {}
    },
    props: {
      datas: {
        type: Array,
        default: []
      }
    },
    methods: {
      getRandomColor() {
        return '#' + ('00000' + (Math.random() * 0x1000000 << 0).toString(16)).substr(-6);
      }
    }
  }
</script>

<style scoped lang="less">
  //边框线的样式
  @borderLine: 0.01rem solid #e0e0e0;
  .mainPage {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    border-top: @borderLine;
    border-left: @borderLine;
    li {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 0.9rem;
      text-align: center;
      box-sizing: border-box;
      font-size: 0.12rem;
      width: 25%;
      overflow: hidden;
      border-bottom: @borderLine;
      border-right: @borderLine;
    }
  }
</style>



