import Vue from 'vue'
import Router from 'vue-router'

/**
 * 绑定一个后退在路由的原型链上，以后后退都调用这个即可
 */
Router.prototype.goBack = function () {
  this.isBack = true
  window.history.go(-1)
}

import HelloWorld from '@/components/HelloWorld'
import sssss from '@/components/sssss.vue'
//基类的滚动
import cpcBaseScroll from '../components/example/scroll/cpcBaseScroll.vue'
//自定义刷新类型-基于基类滚动自定义
import customRefreshScroll from '../components/example/refresh/customRefreshScroll.vue'
//刷新的类型
import cpcRefreshType1 from '../components/example/refresh/cpcRefreshType1.vue'
import cpcRefreshType2 from '../components/example/refresh/cpcRefreshType2.vue'
//动画
import animate from '../components/example/animate/animate.vue'
// card卡片
import card from '../components/example/card/card.vue'
// 顶部状态栏
import topNav from '../components/example/topNav/topNav.vue';

// 带有顶部标题的滚动
import cpcBackHeadAndScroll from '../components/example/scroll/cpcBackHeadAndScroll.vue'
//刷新带头部
import cpcBackHeadAndRefresh from '../components/example/refresh/cpcBackHeadAndRefresh.vue'
//banner
import banner from '../components/example/banner/banner.vue'
//toast
import toast from '../components/example/toast/toast.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }, {
      path: '/sssss',
      name: 'sssss',
      component: sssss
    },
    {
      path: '/cpcBaseScroll',
      name: 'cpcBaseScroll',
      component: cpcBaseScroll
    },
    {
      path: '/customRefreshScroll',
      name: 'customRefreshScroll',
      component: customRefreshScroll
    },
    {
      path: '/cpcRefreshType1',
      name: 'cpcRefreshType1',
      component: cpcRefreshType1
    },
    {
      path: '/cpcRefreshType2',
      name: 'cpcRefreshType2',
      component: cpcRefreshType2
    },
    {
      path: '/animate',
      name: 'animate',
      component: animate
    },
    {
      path: '/card',
      name: 'card',
      component: card
    },
    {
      path: '/topNav',
      name: 'topNav',
      component: topNav
    },
    {
      path: '/cpcBackHeadAndScroll',
      name: 'cpcBackHeadAndScroll',
      component: cpcBackHeadAndScroll
    },
    {
      path: '/cpcBackHeadAndRefresh',
      name: 'cpcBackHeadAndRefresh',
      component: cpcBackHeadAndRefresh
    },
    {
      path: '/banner',
      name: 'banner',
      component: banner
    },
    {
      path: '/toast',
      name: 'toast',
      component: toast
    }
  ]
})



