<template>
  <transition name="slide-fade">
    <!-- 全局提示框 -->
    <div v-show="visible" class="dialog-tips dialog-center">
      {{message}}
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  export default {
    components: {},
    data() {
      return {
        visible: false,
        message: ""
      };
    },
    props: {},
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .dialog-tips {
    border-radius: 0.05rem;
    position: fixed;
    z-index: 100;
    min-width: 0.3rem;
    padding: 0.05rem 0.1rem;
    white-space: nowrap;
    background-color: rgba(0, 0, 0, 0.7);
    box-shadow: 0 0.08rem 0.15rem 0 rgba(0, 0, 0, 0.2);
    text-align: center;
    color: #eee;
  }

  .dialog-center {
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: top .3s ease;
  }

  .slide-fade-leave-active {
    transition: top .3s ease;
  }

  .slide-fade-enter, .slide-fade-leave-to {
    top: 100%;
  }
</style>



