<template>
  <div ref="wrapper" class="cpc-base-scroll">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'

  export default {
    props: {
      /**
       * 1 滚动的时候会派发scroll事件，会截流。
       * 2 滚动的时候实时派发scroll事件，不会截流。
       * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
       */
      probeType: {
        type: Number,
        default: 1
      },
      //点击列表是否派发click事件
      click: {
        type: Boolean,
        default: true
      },
      //是否开启横向滚动
      scrollX: {
        type: Boolean,
        default: false
      },
      //是否派发滚动事件
      scroll: {
        type: Boolean,
        default: false
      },
      //是否派发触摸结束事件
      touchEnd: {
        type: Boolean,
        default: false
      },
      //是否派发列表滚动开始的事件
      beforeScrollStart: {
        type: Boolean,
        default: false
      },
      //是否派发滚动结束事件
      scrollEnd: {
        type: Boolean,
        default: false
      },
      //设置下拉刷新开启的参数
      pullDownRefresh: {
        type: [Boolean, Object],
        default: false
      }
    },
    mounted() {
      this.$nextTick(() => {
        this._initScroll()
      })
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        // better-scroll的初始化
        this.BaseScroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          pullDownRefresh: this.pullDownRefresh,
        })
        // 是否派发滚动事件
        if (this.scroll) {
          let me = this
          this.BaseScroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }
        // 是否派发滚动结束事件
        if (this.scrollEnd) {
          this.BaseScroll.on('scrollEnd', (pos) => {
            this.$emit('scrollEnd', pos)
          })
        }
        //是否派发触摸结束事件
        if (this.touchEnd) {
          this.BaseScroll.on('touchEnd', (pos) => { // 下拉动作
            this.$emit('touchEnd', pos)
          })
        }
        // 是否派发列表滚动开始的事件
        if (this.beforeScrollStart) {
          this.BaseScroll.on('beforeScrollStart', (pos) => {
            this.$emit('beforeScrollStart', pos)
          })
        }
      },
      disable() {
        // 代理better-scroll的disable方法
        this.BaseScroll && this.BaseScroll.disable()
      },
      enable() {
        // 代理better-scroll的enable方法
        this.BaseScroll && this.BaseScroll.enable()
      },
      refresh() {
        // 代理better-scroll的refresh方法
        this.BaseScroll && this.BaseScroll.refresh()
      },
      scrollTo() {
        // 代理better-scroll的scrollTo方法
        this.BaseScroll && this.BaseScroll.scrollTo.apply(this.BaseScroll, arguments)
      },
      scrollToElement() {
        // 代理better-scroll的scrollToElement方法
        this.BaseScroll && this.BaseScroll.scrollToElement.apply(this.BaseScroll, arguments)
      },
      finishPullDown() {
        // 代理better-scroll的scrollToElement方法
        this.BaseScroll && this.BaseScroll.finishPullDown.apply(this.BaseScroll, arguments)
      },
      finishPullUp() {
        // 代理better-scroll的scrollToElement方法
        this.BaseScroll && this.BaseScroll.finishPullUp.apply(this.BaseScroll, arguments)
      }
    }
  }
</script>
<style scoped lang="less">
  .cpc-base-scroll {
    height: 100%;
    overflow: hidden;
    .content {
      position: relative;
      min-height: calc(~"100% + 1px");
    }
  }
</style>



