<template>
  <div class="finished-tick" :class="{'tick':isShow}">
    <div :style="{backgroundColor:bgColor}"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    components: {},
    data() {
      return {}
    },
    props: {
      isShow: {
        type: Boolean,
        default: false
      },
      bgColor: {
        type: String,
        default: "#666"
      }
    },
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .finished-tick {
    position: relative;
    width: 0.2rem;
    height: 0.2rem;
    display: inline-block;
   >div{
     &::before {
       content: '';
       width: 0.02rem;
       height: 0;
       position: absolute;
       top: 0.1rem;
       left: 0;
       background-color: inherit;
       -moz-transform-origin: left top;
       -ms-transform-origin: left top;
       -o-transform-origin: left top;
       -webkit-transform-origin: left top;
       transform-origin: left top;
       -moz-transform: rotate(-45deg);
       -ms-transform: rotate(-45deg);
       -o-transform: rotate(-45deg);
       -webkit-transform: rotate(-45deg);
       transform: rotate(-45deg);
       -moz-animation: tick1 0.4s ease 300ms forwards;
       -o-animation: tick1 0.4s ease 300ms forwards;
       -webkit-animation: tick1 0.4s ease 300ms forwards;
       animation: tick1 0.4s ease 300ms forwards;
     }
     &::after {
       content: '';
       width: 0.02rem;
       height: 0;
       position: absolute;
       top: 0.18rem;
       left: 0.08rem;
       background-color: inherit;
       -moz-transform-origin: left top;
       -ms-transform-origin: left top;
       -o-transform-origin: left top;
       -webkit-transform-origin: left top;
       transform-origin: left top;
       -moz-transform: rotate(-135deg);
       -ms-transform: rotate(-135deg);
       -o-transform: rotate(-135deg);
       -webkit-transform: rotate(-135deg);
       transform: rotate(-135deg);
       -moz-animation: tick2 0.4s ease 300ms forwards;
       -o-animation: tick2 0.4s ease 300ms forwards;
       -webkit-animation: tick2 0.4s ease 300ms forwards;
       animation: tick2 0.4s ease 300ms forwards;
     }
   }
  }

  @keyframes tick1 {
    0% {
      height: 0;
    }
    50% {
      height: 0.1rem;
    }
    100% {
      height: 0.1rem;
    }
  }

  @-webkit-keyframes tick1 {
    0% {
      height: 0;
    }
    50% {
      height: 0.1rem;
    }
    100% {
      height: 0.1rem;
    }
  }

  @-moz-keyframes tick1 {
    0% {
      height: 0;
    }
    50% {
      height: 0.1rem;
    }
    100% {
      height: 0.1rem;
    }
  }

  @keyframes tick2 {
    0% {
      height: 0;
    }
    40% {
      height: 0;
    }
    100% {
      height: 0.2rem;
    }
  }

  @-webkit-keyframes tick2 {
    0% {
      height: 0;
    }
    40% {
      height: 0;
    }
    100% {
      height: 0.2rem;
    }
  }

  @-moz-keyframes tick2 {
    0% {
      height: 0;
    }
    40% {
      height: 0;
    }
    100% {
      height: 0.2rem;
    }
  }

</style>



