<template>
  <cpcBackHeadAndRefresh title="刷新带头部" @loading="loading" @loadMore="loadMore">
    <button @click="lists.push('')">添加</button>
    <cpcSingleRow1 v-for="item in lists" :datas="row"></cpcSingleRow1>
  </cpcBackHeadAndRefresh>
</template>

<script type="text/ecmascript-6">
  import cpcBackHeadAndRefresh from '../../../CPCUI/refresh/headAndRefresh/cpcBackHeadAndRefresh.vue'
  import cpcSingleRow1 from '../../../CPCUI/cards/singleRow/type/cpcSingleRow1.vue'
    export default {
        components: {cpcBackHeadAndRefresh,cpcSingleRow1},
        data() {
            return {
              lists:[],
              row:{
                img: "./static/images/logo.png",
                title: "标题标题标题标题标题标题标题标题",
                describe: "我是描述的内容",
                sellingPrice: 420,
                realPrice: "原价500",
                right: "竞价成功"
              }
            }
        },
        props: {},
        methods: {
          loading(finishPullDown) {
            setTimeout(() => {
              let arr = new Array(5)
              this.lists.unshift(...arr)
              finishPullDown();
            }, 2000)
          },
          loadMore(finishpullUp,setLoadMoreALL) {
            setTimeout(() => {
              let arr = new Array(5)
              this.lists.push(...arr)
              finishpullUp();
              if (arr.length == 0) {
                setLoadMoreALL(true);
              }
            }, 2000)
          }
        },
        computed: {},
        mounted() {

        },
        destroyed() {

        }
    }
</script>

<style scoped lang="less">

</style>



