<template>
  <div class="back-head-and-scroll">
    <cpcTopNavType1 :title="title" :rightTxt="rightTxt"></cpcTopNavType1>
    <cpcRefreshType2 class="base-refresh" @loading="loading" @loadMore="loadMore">
      <slot></slot>
    </cpcRefreshType2>
  </div>
</template>

<script type="text/ecmascript-6">
  import cpcTopNavType1 from '../../topNav/type/cpcTopNavType1.vue';
  import cpcRefreshType2 from '../type/cpcRefreshType2.vue';

  export default {
    components: {cpcTopNavType1, cpcRefreshType2},
    data() {
      return {}
    },
    props: {
      title: {
        type: String,
        default: ""
      },
      rightTxt: {
        type: String,
        default: ""
      }
    },
    methods: {
      loading(finishPullDown) {
        this.$emit('loading',finishPullDown)
      },
      loadMore(finishpullUp,setLoadMoreALL) {
        this.$emit('loadMore',finishpullUp,setLoadMoreALL)
      }
    },
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .back-head-and-scroll {
    height: 100%;
    .base-refresh {
      height: calc(~"100% -  0.5rem");
    }
  }
</style>



