<template>
  <cpcBaseTopNav>
    <i slot="left" @click="$router.goBack()" class="iconfont icon-houtui"></i>
    <p slot="center">
      <span>{{title}}</span>
    </p>
    <span slot="right">{{rightTxt}}</span>
  </cpcBaseTopNav>
</template>

<script type="text/ecmascript-6">
  import cpcBaseTopNav  from '../cpcBaseTopNav.vue';
    export default {
        components: {cpcBaseTopNav},
        data() {
            return {}
        },
        props: {
          title:{
            type:String,
            default:""
          },
          rightTxt:{
            type:String,
            default:""
          }
        },
        methods: {},
        computed: {},
        mounted() {

        },
        destroyed() {

        }
    }
</script>

<style scoped lang="less">

</style>



