<template>
    <div class="single-row3">
      <img class="icon" :src="datas.img" alt="">
      <div>
        <h6 class="title">{{datas.title}}</h6>
        <cpcBaseThreeRow class="price">
          <p slot="left" class="left">
            {{datas.sellingPrice}}
          <span class="single">{{datas.realPrice}}</span>
          </p>
          <div slot="right" class="right">
            <div>{{datas.buy}}</div>
          </div>
        </cpcBaseThreeRow>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
  import cpcBaseThreeRow from '../cpcBaseThreeRow.vue';

  export default {
    components: {cpcBaseThreeRow},
    data() {
      return {

      }
    },
    props: {
      datas:{
        type:Object,
        default:{}
      }
    },
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .single-row3 {
    background-color: #fff;
    .icon{
      border: 0.01rem solid #e0e0e0;
      width: 100%;
      height: 1rem;
    }
    .title{
      font-size: 0.18rem;
      padding: 0.05rem;
    }
    .price{
      padding: 0.05rem;
      .left{
        color: #e00;
        .single{
          color: #666;
          font-size: 0.12rem;
        }
      }
      .right{
        >div{
          font-size: 0.14rem;
          color: #ddd;
          background-color: #e00;
          border-radius: 0.05rem;
          padding: 0.05rem 0.1rem;
        }
      }
    }
  }
</style>



