<template>
  <div class="back-head-and-scroll">
    <cpcTopNavType2 :title="title" @clickLeft="$emit('clickLeft')" @clickRight="$emit('clickRight')"></cpcTopNavType2>
    <cpcBaseScroll class="base-scroll">
      <slot></slot>
    </cpcBaseScroll>
  </div>
</template>

<script type="text/ecmascript-6">
  import cpcTopNavType2 from '../../topNav/type/cpcTopNavType2.vue';
  import cpcBaseScroll from '../baseScroll/cpcBaseScroll.vue';
  export default {
    components: {cpcTopNavType2,cpcBaseScroll},
    data() {
      return {}
    },
    props: {
      title:{
        type:String,
        default:""
      },
      rightTxt:{
        type:String,
        default:""
      }
    },
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .back-head-and-scroll{
    height: 100%;
    .base-scroll{
      height: calc(~"100% -  0.5rem");
    }
  }
</style>



