<template>
  <cpcBaseThreeRow class="single-row1">
    <img class="left" slot="left" :src="datas.img" alt="">
    <div slot="center" class="center">
      <h6 class="title">{{datas.title}}</h6>
      <p class="describe">{{datas.describe}}</p>
      <div class="bott">
        <p class="price">
          <span>¥</span>
          {{datas.sellingPrice}}
          <span class="price2">{{datas.realPrice}}</span>
        </p>
      </div>
    </div>
  </cpcBaseThreeRow>
</template>

<script type="text/ecmascript-6">
  import cpcBaseThreeRow from '../cpcBaseThreeRow.vue';

  export default {
    components: {cpcBaseThreeRow},
    data() {
      return {
      }
    },
    props: {
      datas:{
        type:Object,
        default:{}
      }
    },
    methods: {},
    computed: {},
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style scoped lang="less">
  .single-row1 {
    padding: 0.05rem;
    .left {
      width: 1rem;
      height: 1rem;
      border: 0.01rem solid #e0e0e0;
    }
    .center {
      height: 100%;
      display: flex;
      padding: 0 0.05rem;
      flex-direction: column;
      .title {
        font-size: 0.18rem;
        padding: 0.05rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .describe {
        font-size: 0.12rem;
        color: #666;
        max-height: 0.36rem;
        flex: 1;
        padding: 0.05rem;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
      }
      .bott {
        font-size: 0.12rem;
        padding: 0.05rem;
        .price {
          color: #e00;
        }
        .price2 {
          color: #666;
          text-decoration: line-through;
        }
      }
    }
  }
</style>



